import React from "react";
import NavHeader from "../../components/nav-header";
import Banner from "../../components/banner";
import CategoryLink from "../../components/category-link";
import Course from "../../components/course";
import Footer from "../../components/footer";

import i_banner_1 from "./IMG/最新课程banner@2x.png";
import i_banner_2 from "./IMG/lb.png";

import noudou_1 from "./IMG/首页-明星课程-糖@2x.png";
import noudou_2 from "./IMG/首页-明星课程-补铁@3x.png";
import noudou_3 from "./IMG/首页-明星课程-儿童食品@2x.png";

import ktimg_1 from "./IMG/分类课程-咖喱饭@2x.png";
import ktimg_2 from "./IMG/分类课程-汤圆@2x.png";
import ktimg_3 from "./IMG/分类课程-糖@2x.png";

const imgs = [i_banner_1, i_banner_2];

export default class Home extends React.Component {
    goTo = ({
        currentTarget: {
            dataset: { href },
        },
    }) => {
        this.props.history.push(href);
    };

    handler = { go: this.goTo };
    render() {
        return (
            <div>
                <NavHeader />
                <Banner imgs={imgs} />
                <CategoryLink handler={this.handler} />
                <Course
                    category="star"
                    i_course1={noudou_1}
                    i_course2={noudou_2}
                    i_course3={noudou_3}
                    handler={this.handler}
                    moreHref="/qwer"
                    newesta_1="孩子长不高，竟然是因为吃了太多糖？！"
                    newesta_2="孩子缺铁，你有没有想过是因为“它”吗？"
                    newesta_3="儿童食品更符合孩子需要吗？"
                    hrf_1="https://mp.weixin.qq.com/s/ZzvMAVe_-EEdSH-TvtTKow"
                    hrf_2="https://mp.weixin.qq.com/s/kRydlF0lBjKAssMoCa6SSw"
                    hrf_3="https://mp.weixin.qq.com/s/7eahVQb0SmexuOdEpLydvw"
                />
                <Course
                    category="recent"
                    i_course1={ktimg_1}
                    i_course2={ktimg_2}
                    i_course3={ktimg_3}
                    handler={this.handler}
                    moreHref="/recent_course"
                    newesta_1="孩子食欲不好怎么办？周末和孩子一起做这道菜吧！"
                    newesta_2="[元宵特辑]儿童怎样吃汤圆更好消化?"
                    newesta_3="孩子长不高,竟因吃了太多糖？！"
                    hrf_1="https://mp.weixin.qq.com/s/Tnqff9PcBFfT9ojf0KP5CA"
                    hrf_2="https://mp.weixin.qq.com/s/DqyL7tsOBINvO87MkWIqbg"
                    hrf_3="https://mp.weixin.qq.com/s/ZzvMAVe_-EEdSH-TvtTKow"
                />
                <Footer />
            </div>
        );
    }
}
